import React, { Component } from 'react';
import styled from 'styled-components'
import hover from '../../assets/cursors/hover.png'
import {Link} from 'react-router-dom'
//icons
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import PlayIcon from '../../assets/icons/play.png'

//imagenes
import goimg from '../../assets/products/img/go.png'
import jsimg from '../../assets/products/img/js.png'
import pythonimg from '../../assets/products/img/python.png'
import htmlimg from '../../assets/products/img/html.png'
import cimg from '../../assets/products/img/c.png'
import javaimg from '../../assets/products/img/java.png'
import reactimg from '../../assets/products/img/react.png'
import imgExample from '../../assets/products/img/imgExample.png'


const data =[ 
 
    {
        id:"python",
        img:pythonimg,
        cost:"paid",
        level:"easy",

    },
    {
        id:"js",
        img:jsimg,
        cost:"free",
        level:"medium",

    },
    {
        id:"html",
        img:htmlimg,
        cost:"free",
        level:"easy",

    },
    {
        id:"c",
        img:cimg,
        cost:"paid",
        level:"hard",

    },
    {
        id:"react",
        img:reactimg,
        cost:"paid",
        level:"medium",

    },{
        id:"java",
        img:javaimg,
        cost:"paid",
        level:"easy",

    },
    {
        id:"go",
        img:goimg,
        cost:"free",
        level:"hard",
        titulo:"Experimental Drawings for Picturebooks",
        autor:"by Stografo",
        descripcion:"Un proyecto para manejar tecnologia avanzada de programacion orientada a objetos",
        open:true,
        precio:5,
        descuento:55,
    },
]

class Products extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            id:data.id,
            img:imgExample,
            level:"easy",
            titulo:<h1>Login with API in your web App</h1>,

         }
    }
    render() { 
        return ( 
            <Product id={this.state.id}>

                 <img className="play" src={PlayIcon}/>
                <img className="video" src={this.state.img}/>
                <div className="div">
                    <Link to="/" className="Link"style={linkStyle}>
                    <Level className={this.state.level}> {this.state.level}</Level>
                    {this.state.titulo}</Link>
                    <Skills>
                        <ul>
                            Skills
                            <li>hols</li>
                            <li>hola mas fuerte</li>
                        </ul>
                    </Skills>

                </div>
               
            </Product>
         );
    }
}
 
export default Products;

const Skills = styled.div`
background-color:#f2f2f2;
position:relative;
display: inline-block;
float:right;
width:100%;
list-style: 'none';
`;

const linkStyle ={
    textDecoration: 'none',
    color:'#161616',

}
const Product = styled.div`
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');
width:44%;
height:390px;
max-width: 221.6px;
margin: 10px;
background-color:#fff;
display:inline-block;
.div{
    padding:10px 15px 20px 15px;
}
.Link{
    padding:0px 0 0 0;
    position:relative;
    display: inline-block;
    float:right;
    width:100%;
    //background-color: aqua;
    textDecoration: 'none';
    font-size:8px;
    line-height:25.99px;
    text-align:left;
    font-family: 'Lato', sans-serif;
}
.video{
    width:100%;
    height:33.33%;
  
}
.play:hover{
    background-color:#000;
    opacity:0.5;
   
}
.play{
    width:45px;
    padding:45px 90px 40px 85px;
    
    margin: 0px 0px 0px 0px;
    position: absolute;
}
.easy{
    background-color: #908fbb;
    font-size:10px;
   
}
.medium{
    background-color: #4ea6a6;
    font-size:10px;
    
    
}
.hard{
    background-color: #da6964;
    
}
@media (min-width: 768px) {
    width:221.6px;
}
`;

const Level = styled.div`
line-height:20.99px;
display: inline-block;
font-family: "Press Start 2P";
width:72.4px;
float:left;
height:18.4px;
text-align: center;
margin: 3px 5px 0px 0px;
padding: 0px 0px 0px 0px;
color: #fff;
font-size:13px;
border-radius: 2px;
`;